.model-style-create-category {
  background: #fff;
  border-radius: 5px;
  width: 500px;
  height: 300px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.model-style-create-category .close {
  width: 25px;
  height: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  position: absolute;
  right: 10px;
  top: 5px;
  cursor: pointer;
}
.model-style-create-category form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 0.25rem;
}

.model-style-create-category form > h3 {
  padding: 5px;
  text-transform: uppercase;
  font-weight: bold;
  background-color: var(--bestColor);
  border-radius: 5px;
}

.input-style-create-category {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}
.input-style-create-category > label {
  font-weight: 500;
}

.input-style-create-category > input {
  border-radius: 5px;
  border: 1px solid gray;
  padding: 4px 10px;
  font-size: 15px;
  outline: none;
}
.input-style-create-category > input:focus {
  border: 1.25px solid var(--bestColor);
  transition: all 0.5s ease;
}

.model-style-create-category .btn {
  text-transform: uppercase;
  font-weight: 500;
}

@media only screen and (max-width: 600px) {
  .model-style-create-category {
    background: #fff;
    border-radius: 5px;
    width: 350px;
    height: 230px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .model-style-create-category form > h3 {
    padding: 5px;
    text-transform: uppercase;
    font-weight: bold;
    background-color: var(--bestColor);
    border-radius: 5px;
    font-size: 18px;
  }

  .model-style-create-category form {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 0.25rem;
    text-align: center;
  }

  .model-style-create-category .btn {
    text-transform: uppercase;
    font-weight: 500;
  }
}
