@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@100;700;900&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-family: "Roboto", sans-serif;
  scroll-behavior: smooth;
  overflow-x: hidden;
}

:root {
  --bColor: rgba(0, 0, 0, 0.87);
  --bgDiv: rgba(0, 0, 0, 0.6);
  --text-color1: #eaeaea;
  --text-color2: rgba(0, 0, 0, 0.12);
  --bestColor: rgb(249, 162, 0);
  --box-shaddow: rgba(0, 0, 0, 0.24) 0px 3px 8px; /*box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;*/
}
