.navbar {
  box-shadow: var(--box-shaddow);
  text-transform: uppercase;
}

.nav-link {
  font-weight: 300;
}

.nav-item {
  padding: 2px;
}

.nav-link-auth {
  display: flex;
  height: 100%;
  align-items: center;
  font-weight: 400;
  justify-content: center;
  gap: 1rem;
}

.nav-link.active {
  font-weight: 400;
  background-color: var(--bestColor) !important;
  padding-left: 10px;
  border-radius: 5px;
}

.navbar-brand {
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar-brand > span {
  color: var(--bestColor);
}
.basket {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5rem;
}
.basket > span {
  height: 20px;
  width: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  right: -10px;
  top: -5px;
  background-color: #fff;
  border-radius: 50%;
  font-size: 12px;
  color: black;
}
