.main-contact {
  width: 85%;
  min-height: 600px;
  margin: 6rem auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  padding: 5px;
  box-shadow: var(--box-shaddow);
}

.side-right {
  width: 50%;
}

.side-left {
  width: 50%;
  padding: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  gap: 1rem;
}

.side-left h2 {
  width: 100%;
  font-size: 55px;
  text-transform: uppercase;
  background-color: var(--bestColor);
  display: flex;
  align-items: center;
  justify-content: center;
}

.side-left > p {
  font-size: 20px;
  text-transform: capitalize;
}

.one-contact {
  font-size: 15px;
  font-weight: bold;
  text-transform: capitalize;
  display: flex;
  gap: 1rem;
  align-items: center;
}

.one-contact > span {
  color: var(--bestColor);
  font-size: 20px;
}

.side-right > img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .main-contact {
    width: 90%;
    min-height: 500px;
    margin: 6rem auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    padding: 5px;
    box-shadow: var(--box-shaddow);
  }

  .side-right {
    width: 100%;
  }

  .side-left {
    width: 100%;
    padding: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 1rem;
  }

  .side-left h2 {
    width: 100%;
    font-size: 30px;
    text-transform: uppercase;
    background-color: var(--bestColor);
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .side-left > p {
    font-size: 18px;
    text-transform: capitalize;
  }

  .one-contact {
    font-size: 12px;
    font-weight: bold;
    text-transform: capitalize;
    display: flex;
    gap: 1rem;
    align-items: center;
  }
}
@media (min-width: 600px) and (max-width: 768px) {
  .main-contact {
    width: 90%;
    min-height: 500px;
    margin: 6rem auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1rem;
    padding: 5px;
    box-shadow: var(--box-shaddow);
  }

  .side-right {
    width: 100%;
  }

  .side-left {
    width: 100%;
    padding: 5px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    gap: 1rem;
  }

  .side-left h2 {
    width: 100%;
    font-size: 40px;
    text-transform: uppercase;
    background-color: var(--bestColor);
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .side-left > p {
    font-size: 24px;
    text-transform: capitalize;
  }

  .one-contact {
    font-size: 18px;
    font-weight: bold;
    text-transform: capitalize;
    display: flex;
    gap: 1rem;
    align-items: center;
  }
}
