.user-dashboard {
  height: 600px;
  width: 95%;
  margin: 4rem auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 1rem;
}
.menu {
  width: 19%;
  margin: 4rem 5px;
}

.content {
  width: 85%;
}

/* Admin menu**/
.content-menu-user {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  height: 500px;
  border-radius: 5px;
}

.btn-menu-user {
  width: 100%;
  font-size: 30px;
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 5px;
  justify-content: center;
}

.btn-menu-user:hover {
  cursor: pointer;
  color: var(--bestColor);
  transition: all 0.5s ease;
}

.list-user {
  width: 100%;
  list-style: none;
  font-size: 25px;
  padding: 5px;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  overflow: hidden;
}

.list-user .nav-item .nav-link {
  padding: 5px;
}
.content {
  margin: 5px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.header-person {
  display: flex;
  border-radius: 5px;
  align-items: center;
  justify-content: center;
  letter-spacing: 1px;
  text-transform: capitalize;
}

@media only screen and (max-width: 600px) {
  .user-dashboard {
    height: 600px;
    width: 95%;
    margin: 1rem auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 1rem;
  }
  .menu {
    width: 100%;
    margin: 4rem 5px;
  }

  .content {
    width: 95%;
  }
}

@media (min-width: 600px) and (max-width: 768px) {
  .user-dashboard {
    height: 600px;
    width: 95%;
    margin: 4rem auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    gap: 1rem;
  }
  .menu {
    width: 25%;
    margin: 4rem 5px;
  }

  .content {
    width: 75%;
  }
}
