.model-style-register {
  background: #fff;
  border-radius: 5px;
  width: 550px;
  height: 600px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.model-style-register .close {
  width: 25px;
  height: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  position: absolute;
  right: 10px;
  top: 5px;
  cursor: pointer;
}
.model-style-register form {
  margin-top: 1.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 0.25rem;
}

.model-style-register form > h1 {
  padding: 5px;
  text-transform: uppercase;
  font-weight: bold;
  background-color: var(--bestColor);
  border-radius: 5px;
}

.model-style-register .input-style {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
}

.model-style-register .input-style > label {
  font-weight: 500;
}

.model-style-register .input-style > input {
  border-radius: 5px;
  border: 1px solid gray;
  padding: 4px 10px;
  font-size: 15px;
  outline: none;
}

.model-style-register .input-style > input:focus {
  border: 1.25px solid var(--bestColor);
  transition: all 0.5s ease;
}

.model-style-register .btn {
  text-transform: uppercase;
  font-weight: 500;
}
