.model-create-product {
  background: #fff;
  border-radius: 5px;
  width: 900px;
  height: 650px;
  display: flex;
  flex-direction: column;
  padding: 10px;
  padding-top: 2rem;
}

.model-create-product .close {
  width: 25px;
  height: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  position: absolute;
  right: 10px;
  top: 5px;
  cursor: pointer;
}
.model-content-product form {
  display: flex;
  flex-direction: row;
  gap: 1rem;
  margin: 5px auto;
  display: flex;
  z-index: 100000;
}

.content-data-product {
  width: 100%;
  display: flex;
  gap: 0.5rem;
  flex-direction: column;
}

.model-content-product h3 {
  padding: 5px;
  text-transform: uppercase;
  font-weight: bold;
  background-color: var(--bestColor);
  border-radius: 5px;
  text-align: center;
}

.input-style-create-product {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 0.25rem;
  text-transform: capitalize;
}
.select-category > label,
.input-style-create-product > label {
  font-weight: 500;
  font-size: 15px;
}

.input-style-create-product > input {
  border-radius: 4px;
  border: 1px solid rgb(195, 195, 195);
  padding: 7px 10px;
  font-size: 15px;
  outline: none;
}
.input-style-create-product > input:focus {
  border: 1.25px solid var(--bestColor);
  transition: all 0.5s ease;
}

.select-photo {
  width: 350px;
  padding: 5px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.show-image {
  display: flex;
  gap: 1rem;
  flex-direction: column;
  align-items: center;
}
.content-image {
  height: 250px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-transform: capitalize;
  color: gray;
  border: 2px dotted gray;
}

.content-image img {
  width: 100%;
  height: 100%;
}

@media only screen and (max-width: 600px) {
  .model-create-product {
    background: #fff;
    border-radius: 5px;
    width: 350px;
    height: 550px;
    display: flex;
    flex-direction: column;
    padding: 10px;
    padding-top: 2rem;
    overflow-y: scroll;
    z-index: 100000;
  }

  .model-content-product form {
    display: flex;
    flex-direction: column-reverse;
    gap: 1rem;
    margin: 5px auto;
    display: flex;
  }
  .select-photo {
    width: 330px;
    padding: 5px;
    gap: 1rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
  .show-image {
    display: flex;
    gap: 1rem;
    flex-direction: column;
    align-items: center;
  }
  .content-image {
    height: 250px;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: capitalize;
    color: gray;
    border: 2px dotted gray;
  }

  .content-image img {
    width: 100%;
    height: 100%;
  }
}
