.content-all-products {
  display: flex;
  flex-wrap: wrap;
  gap: 0.5rem;
  padding: 1rem;
  justify-content: center;
}
.one-product {
  width: 300px;
  height: 400px;
  border-radius: 5px;
  position: relative;
}

.image-product {
  width: 100%;
  height: 250px;
}

.image-product > img {
  border-radius: 5px 5px 0px 0px;
  width: 100%;
  height: 100%;
}

.info-product {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
}

.price-product,
.title-product {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  align-items: center;
  height: 100%;
  padding: 5px;
}

.title-product span:last-of-type,
.price-product span:last-of-type {
  font-weight: bold;
  text-transform: capitalize;
}

.description-product {
  height: 60px;
  font-size: 12px;
  padding: 5px;
}

.btns-product {
  height: 50px;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 5px;
}
