.content-home {
  width: 98%;
  margin: 5rem auto;
  display: flex;
  gap: 1rem;
}

.left-side-home {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 0.5rem;
  align-items: center;
  justify-content: center;
  padding: 0.5rem;
}

.right-side-home {
  min-width: 17%;
  text-transform: capitalize;
  display: flex;
  flex-direction: column;
}

.content-left-side-home {
  width: 80%;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin: 0.75rem 0;
}

.filter-category {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  padding: 5px;
}
.filter-price {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.one-filter {
  padding: 5px;
}

.btn-pageList {
  display: flex;
  justify-content: center;
  padding: 5px;
}

.form-search {
  margin: 5px auto;
  padding: 5px;
  display: flex;
  gap: 0.5rem;
  align-items: center;
  justify-content: center;
  font-weight: 700;
  min-height: 50px;
  width: 100%;
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .content-home {
    width: 98%;
    margin: 5rem auto;
    display: flex;
    gap: 1rem;
  }

  .left-side-home {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 0.5rem;
    align-items: center;
    justify-content: center;
    padding: 0.5rem;
  }

  .filter-category {
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    padding: 2px;
  }

  .form-search {
    margin: 5px auto;
    display: flex;
    gap: 0.2rem;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    font-weight: 700;
    height: 50px;
    width: 100%;
  }

  .header-filter {
    font-size: 12px;
  }

  .btn-rest > button {
    font-size: 12px;
  }
}
