.content-products-cart {
  width: 90%;
  margin: 5rem auto;
  display: flex;
  gap: 1rem;
  padding: 10px;
}

.right-products-cart {
  width: 25%;
  display: flex;
  flex-direction: column;
}

.left-products-cart {
  width: 70%;
  min-height: 500px;
  display: flex;
  flex-direction: column;
}
.header-products-cart {
  border-radius: 0px 0px 5px 5px;
  text-transform: capitalize;
  padding: 0px 10px;
}

.all-products-cart {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  padding-bottom: 1rem;
}

.show-one-cart {
  min-height: 250px;
  width: 90%;
  margin: 5px auto;
  display: flex;
  gap: 1rem;
  text-transform: capitalize;
}

.image-one-cart {
  width: 50%;
  height: 100%;
}

.image-one-cart > img {
  width: 100%;
  height: 100%;
}
.detials-one-cart {
  padding: 5px 10px;
}

.action-one-cart {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  gap: 1rem;
}

.content-btns-one {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.total-one-cart {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.decrease-increase-one-cart {
  display: flex;
  align-items: center;
  gap: 1rem;
}
.header-payment-cart {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.address-user {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-transform: capitalize;
}

.content-related-products {
  width: 90%;
  margin: 1rem auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1rem;
}

.render-related-products {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

