.model-show-product {
  background: #fff;
  border-radius: 5px;
  width: 60%;
  display: flex;
  flex-direction: column;
}

.model-show-product .close {
  width: 25px;
  height: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  position: absolute;
  right: 10px;
  top: 5px;
  cursor: pointer;
}

h5.head-show-product {
  padding: 5px;
  text-transform: uppercase;
  font-weight: bold;
  background-color: var(--bestColor);
  border-radius: 5px;
  text-align: center;
}

.content-show-product {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 1rem;
  width: 100%;
  padding: 10px;
}
.show-right-side {
  width: 400px;
  height: 300px;
  border-radius: 5px;
}

.show-right-side > img {
  width: 100%;
  height: 100%;
  border-radius: 5px;
}

.show-left-side {
  width: 65%;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.show-left-side .category-show-product,
.show-left-side .quantity-show-product {
  display: flex;
  align-items: center;
  gap: 0.3rem;
  padding: 3px;
  text-transform: capitalize;
}

.show-left-side .price-show-product,
.show-left-side .title-show-product {
  display: flex;
  align-items: center;
  gap: 0.3rem;
  padding: 3px;
  text-transform: capitalize;
}

.desc-show-product {
  text-transform: lowercase;
  gap: 0.3rem;
  line-height: 2rem;
  padding: 0 5px;
  font-size: 20px;
}

.desc-show-product > h6 {
  text-transform: capitalize;
}

.show-center-side {
  width: 100%;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0.5rem;
  height: 200px;
}

.btn-other {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}

.btn-increase-decrease {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.show-center-side > span,
.btn-increase-decrease > span {
  font-weight: bold;
}
.related-products {
  display: flex;
  align-items: center;
  gap: 1rem;
  margin: 0;
  padding: 2px 10px;
}

.back-home {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 15rem;
  cursor: pointer;
  color: #dddddd;
}

@media only screen and (max-width: 600px) {
  .model-show-product {
    background: #fff;
    border-radius: 5px;
    width: 90%;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    height: 700px;
  }

  .content-show-product {
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    gap: 1rem;
    width: 100%;
    padding: 10px;
  }

  .show-right-side {
    width: 100%;
    height: 300px;
    border-radius: 5px;
  }

  .show-left-side {
    width: 100%;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    gap: 0.2rem;
  }

  .desc-show-product {
    text-transform: lowercase;
    gap: 0.3rem;
    line-height: 1rem;
    padding: 0 5px;
    font-size: 15px;
  }

  .show-center-side {
    width: 100%;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    height: 100px;
  }
}

@media (min-width: 600px) and (max-width: 768px) {
  .model-show-product {
    background: #fff;
    border-radius: 5px;
    width: 95%;
    display: flex;
    flex-direction: column;
    overflow-y: scroll;
    height: 550px;
  }

  .content-show-product {
    display: flex;
    flex-direction: column-reverse;
    justify-content: center;
    gap: 1rem;
    width: 100%;
    padding: 10px;
  }

  .show-right-side {
    width: 100%;
    height: 300px;
    border-radius: 5px;
  }

  .show-left-side {
    width: 100%;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    gap: 0.2rem;
  }

  .desc-show-product {
    text-transform: lowercase;
    gap: 0.3rem;
    line-height: 1rem;
    padding: 0 5px;
    font-size: 15px;
  }

  .show-center-side {
    width: 100%;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    height: 100px;
  }
}
