.header-table-category {
  margin: 5px;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-around;
  padding: 7px 0px;
}

.all-categories {
  padding: 5px;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.one-category {
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 5px;
}

.name-cat {
  font-size: 20px;
  text-transform: capitalize;
  width: 45%;
  display: flex;
  justify-content: center;
}

.action-categories {
  display: flex;
  justify-content: center;
  gap: 1rem;
  width: 45%;
}
