.admin-dashboard {
  width: 97%;
  margin: 4rem auto;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
.menu {
  width: 15%;
  margin: 4rem 5px;
}

.content {
  width: 85%;
  min-height: 700px;
}

/* Admin menu**/
.content-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem;
  height: 400px;
  border-radius: 5px;
}

.btn-menu-admin {
  width: 100%;
  font-size: 30px;
  display: flex;
  align-items: center;
  gap: 1rem;
  padding: 5px;
  justify-content: center;
}

.btn-menu-admin:hover {
  cursor: pointer;
  color: var(--bestColor);
  transition: all 0.5s ease;
}

.list-admin {
  width: 100%;
  list-style: none;
  font-size: 25px;
  padding: 5px;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  overflow: hidden;
}

.list-admin .nav-item .nav-link {
  padding: 5px;
}
.content {
  margin: 5px;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.header-person {
  display: flex;
  border-radius: 5px;
  align-items: center;
  justify-content: center;
  letter-spacing: 1px;
  text-transform: capitalize;
}

.info-person {
  display: flex;
  flex-direction: column;
  text-transform: capitalize;
  padding: 5px 10px;
}

.content-all-orders {
  display: flex;
  flex-direction: column;
}
.header-all-orders {
  padding: 5px;
  display: flex;
  flex-direction: row;
}

.order-qauntity,
.order-payment,
.order-date,
.order-buyer,
.order-status {
  width: 30%;
  display: flex;
  justify-content: center;
  font-size: 20px;
  font-weight: bold;
}
.order-id {
  width: 10%;
  display: flex;
  justify-content: center;
  font-size: 20px;
  font-weight: bold;
}

.content-one-order {
  display: flex;
  flex-direction: column;
}

.content-data-order {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 5px;
}

.content-data-order .order-id,
.content-data-order .order-qauntity,
.content-data-order .order-payment,
.content-data-order .order-date,
.content-data-order .order-buyer,
.content-data-order .order-status {
  font-size: 18px;
  font-weight: 500;
}

.content-order-products {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1rem;
  padding: 5px;
  overflow: hidden;
}

.one-order-product {
  height: 200px;
  display: flex;
}

.content-order-products .image-one-cart {
  width: 25%;
  height: 100%;
}

.content-order-products .image-one-cart > img {
  width: 100%;
  height: 100%;
}

.one-order-product .detials-one-cart {
  width: 75%;
}

@media only screen and (max-width: 600px) {
  .admin-dashboard {
    width: 97%;
    margin: 2rem auto;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
  }
  .menu {
    width: 100%;
    margin: 4rem 5px;
  }

  .content {
    width: 95%;
    min-height: 700px;
  }
}

@media (min-width: 600px) and (max-width: 768px) {
  .admin-dashboard {
    width: 97%;
    margin: 4rem auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    border: 1px solid green;
  }
  .menu {
    width: 25%;
    margin: 4rem 5px;
  }

  .content {
    width: 75%;
    min-height: 700px;
  }
}
