.content-cart {
  width: 100%;
  height: 130px;
  margin: 10px 0;
  display: flex;
  align-items: center;
  gap: 0.2rem;
  position: relative;
}

.cart-image {
  width: 40%;
  height: 100%;
}
.cart-info-product {
  height: 100%;
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.cart-image > img {
  width: 100%;
  height: 100%;
}
.cart-name,
.cart-counter,
.cart-total-price,
.cart-price {
  display: flex;
  flex-direction: row;
  gap: 5px;
  width: 100%;
  text-transform: capitalize;
}

.cart-delete {
  position: absolute;
  top: 0px;
  right: 0px;
  font-size: 20px;
}
